$file-prefix: 'home-verifyDetail';

.#{$file-prefix}-content {
    margin-top: 20px;
    color: #ccc;
}

.#{$file-prefix}-image-layout {
    display: grid;
    width: 100%;
    grid-row-gap: 20px;
    grid-column-gap: 20px;
    grid-template-columns: repeat(3, 30%);
    grid-template-rows: 30%;

    .#{$file-prefix}-image {
        width: 100%;
    }
}
.#{$file-prefix}-btn-layout {
    margin-top: 20px;
    display: flex;
    justify-content: space-around;
}